}

#gitt {
    margin-left: 15px;

    margin-top: 40px;
    margin-bottom: -20px;
    transition: -webkit-transform 0.8s;
    transition: transform 0.8s;
    transition: transform 0.8s, -webkit-transform 0.8s;
}
#gitt:hover {
    -webkit-transform: scale(1.9);
    transform: scale(1.9);
}

